<template>
<BaseDialog v-bind="$attrs" v-on="$listeners">
    <div class="dialog-title title-color text-align-left">Tm kim</div>
    <div class="dialog-body text-align-left">

        <!---->
        <el-form ref="form" :model="form" label-width="120px">
            <el-form-item label="Nhp t kha">
                <el-autocomplete style="width:100%"  v-model="state1" :fetch-suggestions="querySearchAsync" v-bind:placeholder="$t('Home.placeholdersearch')" @select="handleSelect"></el-autocomplete>

            </el-form-item>
            <el-form-item label="Chn a im">
                <el-cascader :options="options" placeholder="Chn" :props="props" collapse-tags></el-cascader>
            </el-form-item>
            <el-form-item label="Danh mc">
                <el-select v-model="form.region" placeholder="Chn danh mc">
                    <el-option label="Danh mc 1" value="danhmuc1"></el-option>
                    <el-option label="Danh mc 2" value="danhmuc2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="T kha HOT">
                <el-button size="small" round>Ks cp i</el-button>
                <el-button size="small" round>Ks du lch</el-button>
                <el-button size="small" round>Gh tnh yu</el-button>
                <el-button size="small" round>Romatic</el-button>
                <el-button size="small" round>KSTinhYeu</el-button>
            </el-form-item>
            <el-form-item label="Mc gi (VN)">
                <el-slider :max="3000000" label="VN" :min="0" v-model="value1" :step="100000">
                </el-slider>
            </el-form-item>
        </el-form>
    </div>
    <div class="dialog-footer">
        <button class="btn btn-color router-link-active" type="button" @click="$emit('update:active', false)">
            <router-link class="router-link-active" tag="a" :to="{ name: 'Category', params: { Sn: 1 }}">p dng</router-link>
        </button>
    </div>
</BaseDialog>
</template>
<script>
import axios from "axios"
import BaseDialog from "./BaseDialog.vue";
const cityOptions = ['Gim gi c bit', 'Khuyn mi', 'Khch sn mi', 'Khch sn ni bt', 'nh 360'];
export default {
    name: "AlertDialog",
    components: {
        BaseDialog
    },
    props: {
        title: String,
        content: String,
        confirmText: {
            type: String,
            default: "Ok"
        }
    },
    data() {
        return {
            value1: 0,
            props: {
                multiple: true
            },
            state1: '',
            options: [{
                value: 1,
                label: 'Min Bc',
                children: [{
                    value: 2,
                    label: 'H Ni',
                    children: [{
                            value: 3,
                            label: 'Ba nh'
                        },
                        {
                            value: 4,
                            label: 'ng a'
                        },
                        {
                            value: 5,
                            label: 'Hon kim'
                        }
                    ]
                }, {
                    value: 6,
                    label: 'Hi Phng',
                    children: [{
                            value: 7,
                            label: ' Sn'
                        },
                        {
                            value: 8,
                            label: 'Kin An'
                        },
                        {
                            value: 9,
                            label: 'Thy Nguyn'
                        }
                    ]
                }, {
                    value: 10,
                    label: 'Hi Dng',
                    children: [{
                            value: 11,
                            label: 'T K'
                        },
                        {
                            value: 12,
                            label: 'Thnh ph Hi Dng'
                        },
                        {
                            value: 13,
                            label: 'Thanh Min'
                        }
                    ]
                }]
            }, {
                value: 14,
                label: 'Min Trung',
                children: [{
                    value: 15,
                    label: 'Hu',
                    children: [{
                            value: 16,
                            label: 'Qung in'
                        },
                        {
                            value: 17,
                            label: 'Qung in'
                        },
                        {
                            value: 18,
                            label: 'Hng Thy'
                        }
                    ]
                }, {
                    value: 19,
                    label: ' Nng',
                    children: [{
                            value: 20,
                            label: 'Sn Tr'
                        },
                        {
                            value: 21,
                            label: 'Cm L'
                        },
                        {
                            value: 22,
                            label: 'Hi Chu'
                        }
                    ]
                }]
            }, {
                value: 23,
                label: 'Min Nam',
                children: [{
                    value: 24,
                    label: 'TP H Ch Minh',
                    children: [{
                            value: 25,
                            label: 'Qun 1'
                        },
                        {
                            value: 26,
                            label: 'Qun 2'
                        },
                        {
                            value: 27,
                            label: 'Qun 5'
                        }
                    ]
                }, {
                    value: 28,
                    label: 'ng Nai',
                    children: [{
                            value: 29,
                            label: 'TP Bin Ha'
                        },
                        {
                            value: 30,
                            label: 'Trng Bom'
                        },
                        {
                            value: 31,
                            label: 'Nhn Trch'
                        }
                    ]
                }]
            }],

            checkboxGroup2: ['Gim gi c bit'],
            cities: cityOptions,
            value2: 0,
            form: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },

        };
    },

    methods: {
        loadAll() {
            return [{
                    "value": "Khong cch",
                    "link": "#"
                },
                {
                    "value": "Gi tng dn",
                    "link": "#"
                },
                {
                    "value": "Gi gim dn ",
                    "link": "#"
                },
                {
                    "value": "Xp hng v Bnh chn",
                    "link": "#"
                },
                {
                    "value": "Hot nht",
                    "link": "#"
                },
                {
                    "value": "Khuyn mi",
                    "link": "#"
                },
                {
                    "value": "Khch sn mi",
                    "link": "#"
                }
            ];
        },
        
        querySearchAsync(queryString, cb) {
            var links = this.links;
            var results = queryString ? links.filter(this.createFilter(queryString)) : links;

            clearTimeout(this.timeout);
            this.timeout = setTimeout(() => {
                cb(results);
            }, 3000 * Math.random());
        },
        createFilter(queryString) {
            return (link) => {
                return (link.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        handleSelect(item) {
            console.log(item);
        },
        onSubmit() {
            console.log('Kt thc!');
        },
        handleRemove(file) {
            console.log(file);
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        handleDownload(file) {
            console.log(file);
        },
        changeLocale(locale) {
            i18n.locale = locale;
        },
        formatTooltip(val) {
            return val / 10;
        }

    },
    mounted() {
        this.links = this.loadAll();
    }

};
</script>

<style scoped>
.form-control {
    width: 100%;
    height: 30px;
    padding: 0;
}

.dialog-title {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 18px;
}


.router-link-active {
    color: #ffffff
}

.router-link-active:hover {
    -webkit-transform: scale(1.1);
    text-decoration: none;
}

.dialog-footer {
    text-align: right;
    margin-top: 20px;
}

.dialog-footer button {
    padding: 5px 10px;
    font-size: 16px;
    cursor: pointer;
}

.text-align-left {
    text-align: left;
}

.btn-color {
    background-color: #ff6400;
    color: #ffffff;
}
</style>



